exports.push([module.i, "\n.gameActionButton[data-v-78448637] {\n  margin-top: 5px;\n}\n", "", {"version":3,"sources":["/home/travis/build/steemgg/steemgg-website/ui/src/components/shared/src/components/shared/GamesTable.vue?0df3a9d8"],"names":[],"mappings":";AA0LA;EACA,gBAAA;CACA","file":"GamesTable.vue","sourcesContent":["<template>\n  <div>\n    <el-table :data=\"items\" style=\"width: 100%\" :stripe=true v-loading=\"loading\" :empty-text=\"'None'\">\n      <el-table-column prop=\"id\" label=\"ID\" width=\"100\">\n      </el-table-column>\n      <el-table-column label=\"Name\" width=\"150\">\n        <template slot-scope=\"scope\">\n          <el-button @click=\"viewDetails(scope.$index)\" type=\"text\" size=\"small\">{{scope.row.title}}</el-button>\n        </template>\n      </el-table-column>\n      <el-table-column prop=\"category\" label=\"Category\" width=\"120\">\n      </el-table-column>\n      <el-table-column label=\"Create Time\" width=\"150\">\n        <template slot-scope=\"scope\">\n          {{transformTime(scope.row.created)}}\n        </template>\n      </el-table-column>\n      <el-table-column label=\"Last Modified\" width=\"150\">\n        <template slot-scope=\"scope\">\n          {{transformTime(scope.row.lastModified)}}\n        </template>\n      </el-table-column>\n      <el-table-column prop=\"payout\" label=\"Reward\" width=\"80\">\n      </el-table-column>\n      <el-table-column label=\"Audit History\" width=\"150\">\n        <template slot-scope=\"scope\">\n          <comment-popover :comments=\"scope.row.commentHistory\"></comment-popover>\n        </template>\n      </el-table-column>\n      <el-table-column label=\"Operations\" fixed=\"right\">\n        <template slot-scope=\"scope\">\n          <el-button v-if=\"scope.row.status == 0 && type == 'audit'\" @click=\"openDialog(scope.$index, 'Approve')\" type=\"success\"  size=\"mini\">Approve</el-button>\n          <div class=\"gameActionButton\" v-if=\"scope.row.status == 1\"><el-button  @click=\"openDialog(scope.$index, 'Deny Game')\" type=\"danger\"  size=\"mini\" v-if=\"type == 'report' || type == 'live'\">Deny Game</el-button></div>\n          <div class=\"gameActionButton\" v-if=\"scope.row.report == 1\"><el-button @click=\"openDialog(scope.$index, 'Dismiss Report')\" type=\"primary\"  size=\"mini\" v-if=\"type == 'report'\">Dismiss Report</el-button></div>\n          <div class=\"gameActionButton\" v-if=\"scope.row.status == 1 && type === 'recommended'\"><el-button  @click=\"removeGameFromRecomended(scope.$index)\" type=\"danger\" size=\"mini\" v-if=\"type == 'recommended'\">Remove</el-button></div>\n        </template>\n      </el-table-column>\n    </el-table>\n    <el-dialog :title=\"actionTitle\" :visible.sync=\"dialogFormVisible\">\n      <el-form :model=\"form\">\n        <el-form-item label=\"Comment\" :label-width=\"labelWidth\">\n          <el-input :rows=\"2\" v-model=\"form.comment\" auto-complete=\"off\" type=\"textarea\"></el-input>\n        </el-form-item>\n      </el-form>\n      <div slot=\"footer\" class=\"dialog-footer\">\n        <el-button @click=\"confirmDialogCancel\">Cancel</el-button>\n        <el-button type=\"primary\" @click=\"confirmDialogAction\" :disabled=\"commentIsEmpty\">Confirm</el-button>\n      </div>\n    </el-dialog>\n  </div>\n</template>\n\n<script>\n  import moment from 'moment'\n  import GameService from '../../service/game.service'\n  import CommentPopover from './CommentPopover.vue'\n  const gameService = new GameService()\n\n  export default {\n\n    components: {\n      CommentPopover\n    },\n    props: ['items', 'type'],\n    name: 'GamesTable',\n    data () {\n      return {\n        dialogFormVisible: false,\n        activeIndex: null,\n        actionTitle: '',\n        form: {\n          comment: ''\n        },\n        labelWidth: '80px',\n        loading: false\n      }\n    },\n    methods: {\n      viewDetails (index) {\n        console.log(`check details of ${index}`)\n        this.$router.push({\n          name: 'viewGame',\n          params: {\n            id: this.items[index].id\n          }\n        })\n      },\n      openDialog (index, type) {\n        this.dialogFormVisible = true\n        this.activeIndex = index\n        this.actionTitle = type\n      },\n      confirmDialogCancel () {\n        this.dialogFormVisible = false\n        this.form.comment = ''\n      },\n      confirmDialogAction () {\n        if (this.form.comment.trim().length === 0) {\n          this.$alert('Comment cannot be empty!')\n        } else {\n          if (this.actionTitle === 'Approve') {\n            this.approve()\n          } else if (this.actionTitle === 'Deny Game') {\n            this.deny()\n          } else if (this.actionTitle === 'Dismiss Report') {\n            this.clear()\n          }\n          this.form.comment = ''\n        }\n      },\n      approve () {\n        this.dialogFormVisible = false\n        console.log(`approve details of ${this.activeIndex} with comment`)\n        this.loading = true\n        gameService.approve(this.items[this.activeIndex].id, this.form.comment).then(() => {\n          this.$message.success('Game is approved.')\n          this.items.splice(this.activeIndex, 1)\n          this.$emit('gameApproved')\n        }).catch(error => {\n          console.log(error)\n          this.$message.error('Approve action failed.')\n        }).finally(() => {\n          this.form.comment = ''\n          this.loading = false\n        })\n      },\n      deny () {\n        this.dialogFormVisible = false\n        console.log(`deny details of ${this.activeIndex}`)\n        this.loading = true\n        gameService.deny(this.items[this.activeIndex].id, this.form.comment).then(() => {\n          this.$message.success('Game is denied.')\n          this.items.splice(this.activeIndex, 1)\n          this.$emit('gameDenied')\n        }).catch(error => {\n          console.log(error)\n          this.$message.error('Deny action failed.')\n        }).finally(() => {\n          this.form.comment = ''\n          this.loading = false\n        })\n      },\n      clear () {\n        this.dialogFormVisible = false\n        console.log(`clearn report  of ${this.activeIndex}`)\n        this.loading = true\n        gameService.undoReport(this.items[this.activeIndex].id, this.form.comment).then(() => {\n          this.$message.success('Game report status is cleared.')\n          this.items.splice(this.activeIndex, 1)\n          this.$emit('gameReportCleared')\n        }).catch(error => {\n          console.log(error)\n          this.$message.error('undo report action failed.')\n        }).finally(() => {\n          this.form.comment = ''\n          this.loading = false\n        })\n      },\n      removeGameFromRecomended (index) {\n        this.loading = true\n        gameService.undoRecommend(this.items[index].id).then(() => {\n          this.$message.success('Game is removed from recommended list.')\n          this.items.splice(this.activeIndex, 1)\n          this.$emit('gameUndoRecommended')\n        }).catch(error => {\n          console.log(error)\n          this.$message.error('remove game from recommended list action failed.')\n        }).finally(() => {\n          this.loading = false\n        })\n      },\n      transformTime (time) {\n        let result = moment(time).format('DD/MM/YYYY, h:mm')\n        return result\n      }\n    },\n    computed: {\n      commentIsEmpty () {\n        return this.form.comment == null || this.form.comment.trim().length === 0\n      }\n    },\n    mounted () {\n    }\n  }\n</script>\n<style scoped>\n  .gameActionButton {\n    margin-top: 5px;\n  }\n</style>\n"],"sourceRoot":""}]);

