exports.push([module.i, "\n.privacy {\n  margin: 40px;\n}\n", "", {"version":3,"sources":["/home/travis/build/steemgg/steemgg-website/ui/src/components/views/src/components/views/Privacy.vue?0ad69592"],"names":[],"mappings":";AA+OA;EACA,aAAA;CACA","file":"Privacy.vue","sourcesContent":["<template>\n  <div>\n    <el-header>\n      <common-header></common-header>\n    </el-header>\n    <div class=\"markdown-body privacy\">\n\n      <h1 id=\"privacypolicy\">Privacy Policy</h1>\n\n      <p>Effective 1st July, 2018</p>\n\n      <ol>\n        <li>We want you to understand how and why (steemgg, we or us) collects, uses, and shares information about you when you access and use steemggs websites, mobile apps, widgets, and other online products and services (collectively, the \"Services\") or when you otherwise interact with us.</li>\n      </ol>\n\n      <h2 id=\"informationyouprovidetous\">Information You Provide to Us</h2>\n\n      <ol>\n        <li><p>We collect information you provide directly to us when you use our Services. Depending on which Service you use, we may collect different information about you.</p>\n\n          <p>This includes:</p></li>\n      </ol>\n\n      <h2 id=\"informationregardingyouruseoftheservices\">Information Regarding Your Use of the Services</h2>\n\n      <ol>\n        <li>We collect the content and other information you provide when you use our Services. This includes information used to create your account (e.g., a username, an email address, phone number), account preferences, and the content of information you post to the Services (e.g., text, photos, videos, links).</li>\n      </ol>\n\n      <h2 id=\"transactionalinformation\">Transactional Information</h2>\n\n      <ol>\n        <li>If you purchase products or services from us (e.g., Steem Power), we will collect certain information from you, including your name, address, email address, and information about the product or service you are purchasing. Payments are processed by third-party payment processors (e.g., Stripe and PayPal), so please refer to the applicable processors terms and privacy policy for more information about how payment information is processed and stored.</li>\n      </ol>\n\n      <h2 id=\"otherinformation\">Other Information</h2>\n\n      <ol>\n        <li>You may choose to provide other information directly to us. For example, we may collect information when you fill out a form, participate in contests, sweepstakes or promotions, apply for a job, communicate with us via third-party sites and services, request customer support or otherwise communicate with us.</li>\n      </ol>\n\n      <h2 id=\"informationwecollectautomatically\">Information We Collect Automatically</h2>\n\n      <ol>\n        <li>When you access or use our Services, we may also automatically collect information about you. This includes:</li>\n      </ol>\n\n      <h2 id=\"logandusagedata\">Log and Usage Data</h2>\n\n      <ol>\n        <li>We may log information when you access and use the Services. This may include your IP address, user-agent string, browser type, operating system, referral URLs, device information (e.g., device IDs), pages visited, links clicked, user interactions (e.g., voting data), the requested URL, hardware settings, and search terms.</li>\n      </ol>\n\n      <h2 id=\"informationcollectedfromcookies\">Information Collected from Cookies</h2>\n\n      <ol>\n        <li>We may receive information from cookies, which are pieces of data your browser stores and sends back to us when making requests. We use this information to improve your experience, understand user activity, and improve the quality of our Services. For example, we store and retrieve information about your preferred language and other settings. On how you can disable cookies, please see Your Choices below. For more information about cookies read our cookie policy section <a href=\"https://steemgg.com/#/cookies\">https://steemgg.com/cookies</a> </li>\n      </ol>\n\n      <h2 id=\"locationinformation\">Location Information</h2>\n\n      <ol>\n        <li>With your consent, we may collect information about the specific location of your mobile device (for example, by using GPS or Bluetooth). You can revoke this consent at any time by changing the preferences on your device, but doing so may affect your ability to use all of the features and functionality of our Services.</li>\n      </ol>\n\n      <h2 id=\"socialsharing\">Social Sharing</h2>\n\n      <ol>\n        <li>We may offer social sharing features or other integrated tools that let you share content or actions you take on our Services with other media. Your use of these features enables the sharing of certain information with your friends or the public, depending on the settings you establish with the third party that provides the social sharing feature. For more information about the purpose and scope of data collection and processing in connection with social sharing features, please visit the privacy policies of the third parties that provide these social sharing features (e.g., Tumblr, Facebook, Reddit, Pinterest, and Twitter).</li>\n      </ol>\n\n      <h2 id=\"howweuseinformationaboutyou\">How We Use Information About You</h2>\n\n      <ol>\n        <li><p>We use information about you to:</p>\n\n          <p>Provide, maintain, and improve the Services;</p>\n\n          <p>Help protect the safety of steemgg and our users, which includes blocking suspected spammers, addressing abuse, and enforcing the steemgg user agreement and the Terms of Service;</p>\n\n          <p>Send you technical notices, updates, security alerts, invoices and other support and administrative messages;</p>\n\n          <p>Provide customer service;</p>\n\n          <p>Communicate with you about products, services, offers, promotions, and events, and provide other news and information we think will be of interest to you (for information about how to opt out of these communications, see Your Choices below);</p>\n\n          <p>Monitor and analyze trends, usage, and activities in connection with our Services;</p>\n\n          <p>Personalize the Services and provide advertisements, content and features that match user profiles or interests.</p></li>\n      </ol>\n\n      <h2 id=\"howweshareinformation\">How We Share Information</h2>\n\n      <ol>\n        <li><p>When you use the Services, certain information may be shared with other users and the public. For example:</p>\n\n          <p>When you submit a post or comment to the Services, visitors to and users of our Services will be able to see the content of your posts and comments, the username associated with your posts or comments, and the date and time you originally submitted the post or comment. Although some parts of the Services may be private or quarantined, they may become public and you should take that into consideration before posting to the Services.</p>\n\n          <p>When other users view your profile, they will be able to see information about your activities on the Services, such as your username, prior posts and comments, Steem Power, and how long you have been a member of the Services.</p></li>\n\n        <li><p>Please note that, even when you delete your account, the posts, comments and messages you submit through the Services may still be viewable or available on our servers. For more information, see Your Choices below.</p></li>\n\n        <li><p>We will not share, sell, or give away any of our users personal information to third parties, unless one of the following circumstances applies:</p>\n\n          <p>Except as it relates to advertisers and our ad partners, we may share information with vendors, consultants, and other service providers who need access to such information to carry out work for us;</p>\n\n          <p>If you participate in contests, sweepstakes, promotions, special offers, or other events or activities in connection with our Services, we may share information with entities that partner with us to provide these offerings;</p>\n\n          <p>We may share information (and will attempt to provide you with prior notice, to the extent legally permissible) in response to a request for information if we believe disclosure is in accordance with, or required by, any applicable law, regulation, legal process or governmental request;</p>\n\n          <p>We may share information in response to an emergency if we believe it's necessary to prevent imminent and serious bodily harm to a person;</p>\n\n          <p>We may share information if we believe your actions are inconsistent with our user agreements, or other steemgg policies, or to protect the rights, property, and safety of ourselves and others;</p>\n\n          <p>We may share information between and among steemgg, and its current and future parents, affiliates, subsidiaries, and other companies under common control and ownership; and</p>\n\n          <p>We may share information with your consent or at your direction.</p></li>\n\n        <li><p>We may share aggregated or de-identified information, which cannot reasonably be used to identify you.</p></li>\n      </ol>\n\n      <h2 id=\"adsandanalyticspartners\">Ads and Analytics Partners</h2>\n\n      <ol>\n        <li>We may partner with third-party advertisers, ad networks, and analytics providers to deliver advertising and content targeted to your interests and to better understand your use of the Services. These third parties may collect information sent by your computer, browser, or mobile device in response to a request for content, such as unique identifiers, your IP address, or other information about your computer or device. For example:</li>\n      </ol>\n\n      <h2 id=\"advertisersandadnetworks\">Advertisers and Ad Networks</h2>\n\n      <ol>\n        <li>Our ad partners and network may use cookies and use related technologies to collect information when ads are delivered to you on our Services, but steemgg does not link to or provide your actual steemgg account details to these advertising partners. This means that steemgg does not share your individual account browsing habits with advertisers. Steemgg cannot see advertisers cookies and advertisers will not see steemgg cookies.</li>\n      </ol>\n\n      <h2 id=\"analyticspartners\">Analytics Partners</h2>\n\n      <ol>\n        <li><p>We use analytics partners (such as Google Analytics) to help analyze usage and traffic for our Services. As an example, we may use analytics partners to analyze and measure, in the aggregate, the number of unique visitors to our Services.</p></li>\n\n        <li><p>For more information about how you may control the collection and/or use of information for advertising and analytics purposes, please see Your Choices.</p></li>\n      </ol>\n\n      <h2 id=\"security\">Security</h2>\n\n      <ol>\n        <li>We take reasonable measures to help protect information about you from loss, theft, misuse and unauthorized access, disclosure, alteration, and destruction.</li>\n      </ol>\n\n      <h2 id=\"childrenunder18\">Children under 18</h2>\n\n      <ol>\n        <li>Although we welcome users from all walks of life, steemgg is not intended or directed at individuals under the age of 18. Therefore, individuals under the age of 18 may not create an account or otherwise access or use the Services.</li>\n      </ol>\n\n      <h2 id=\"yourchoices\">Your Choices</h2>\n\n      <ol>\n        <li>As a steemgg user, you have choices about how to protect and limit the collection, use, and disclosure of, information about you. This includes:</li>\n      </ol>\n\n      <h2 id=\"preferences\">Preferences</h2>\n\n      <ol>\n        <li>We may provide you with tools and preference settings that allow you to access, correct, delete, and modify information associated with your account.</li>\n      </ol>\n\n      <h2 id=\"accountinformation\">Account Information</h2>\n\n      <ol>\n        <li>You may delete your account information at any time by logging into your account and following the steps under Preferences. When you delete your account, your profile is no longer visible to other users and disassociated from content you posted under that account. Please note, however, that the posts, comments, and messages you submitted prior to deleting your account will still be visible to others, unless you delete such content. We may also retain certain information about you as required by law or for legitimate business purposes after you delete your account. Any information that has been published to the Steem blockchain will also remain indefinitely.</li>\n      </ol>\n\n      <h2 id=\"cookies\">Cookies</h2>\n\n      <ol>\n        <li>Most web browsers are set to accept cookies by default. If you prefer, you can usually choose to set your browser to remove or reject first- and third-party cookies. Please note that if you choose to remove or reject cookies, this could affect the availability and functionality of our Services. For more information about cookies read our cookie policy section <a href=\"https://steemgg.com/#/cookies\">https://steemgg.com/cookies</a> </li>\n      </ol>\n\n      <h2 id=\"thirdpartyadvertisingandanalytics\">Third-Party Advertising and Analytics</h2>\n\n      <ol>\n        <li>Some analytics providers may provide specific opt-out mechanisms (e.g. Google Analytics Opt-out), and we will provide, as needed, additional tools and third-party services that allow you to better understand cookies and how you can opt-out. You have the ability to opt out of having your web browsing information used for behavioral advertising purposes. For more information about behavioral advertising, or to opt out, please visit<a href=\"http://www.aboutads.info/choices\">www.aboutads.info/choices</a>.</li>\n      </ol>\n\n      <h2 id=\"donottrack\">Do Not Track</h2>\n\n      <ol>\n        <li>Do Not Track (DNT) is a privacy preference that you can set in most web browsers. We support DNT in our Services. There is no accepted standard on how to respond to web browsers DNT signals. When you have DNT enabled, we may still use information collected for analytics and measurement purposes or to otherwise provide our Services (e.g., <a href=\"https://steemgg.com\">steemgg.com</a> buttons), but we will not load any third-party trackers. You may, however, opt out of having information about you collected and used for behavioral advertising purposes, as described above. </li>\n      </ol>\n\n      <h2 id=\"promotionalcommunications\">Promotional Communications</h2>\n\n      <ol>\n        <li>You may opt out of receiving promotional communications from us by following the instructions in those communications. If you opt out, we may still send you non-promotional communications, such as information about your account or your use of our Services.</li>\n      </ol>\n\n      <h2 id=\"mobilenotifications\">Mobile Notifications</h2>\n\n      <ol>\n        <li>With your consent, we may send promotional and non-promotional push notifications or alerts to your mobile device. You can deactivate these messages at any time by changing the notification settings on your mobile device.</li>\n      </ol>\n\n      <h2 id=\"internationaldatatransfers\">International Data Transfers</h2>\n\n      <ol>\n        <li>We are based in the Italy and the information we collect is governed by Italy law. By accessing or using the Services or otherwise providing information to us, you consent to the processing, transfer and storage of information in and to Italy and other countries, where you may not have the same rights as you do under local law.</li>\n      </ol>\n\n      <h2 id=\"changes\">Changes</h2>\n\n      <ol>\n        <li>We may change this Privacy Policy from time to time. If we do, we will let you know by revising the date at the top of the policy. If we make a change to this policy that, in our sole discretion, is material, we will provide you with additional notice (such as adding a statement to steemouncements, the front page of the Services or sending you a notification). We encourage you to review the Privacy Policy whenever you access or use our Services or otherwise interact with us to stay informed about our information practices and the ways you can help protect your privacy. If you continue to use our Services after Privacy Policy changes go into effect, you consent to the revised policy.</li>\n      </ol>\n\n      <h2 id=\"contactus\">Contact Us</h2>\n\n      <ol>\n        <li>If you have any questions about this Privacy Policy, please email <a href=\"mailto:privacy@steemgg.com\">privacy@steemgg.com</a></li>\n      </ol>\n    </div>\n    <el-footer>\n      <common-footer></common-footer>\n    </el-footer>\n  </div>\n</template>\n<script>\n  import CommonFooter from '../common/CommonFooter'\n  import CommonHeader from '../common/CommonHeader'\n  export default {\n    components: {\n      CommonFooter,\n      CommonHeader\n    },\n    props: [],\n    name: 'Privacy',\n    data () {\n    }\n  }\n</script>\n<style>\n  .privacy {\n    margin: 40px;\n  }\n</style>\n"],"sourceRoot":""}]);

